import React from 'react';
import { GetServerSideProps } from 'next';
import { Todo } from '../../modules/Todo';
import { todoModel } from '../../shared/api/todoModel';
import { todosRequests } from '../../shared/api/todos';

const TodoPage: React.FC = (props) => {
    console.log(props, 'page');

    return <Todo todo={props.pageProps.todo} />;
};

const { fetchTodoById } = todoModel;

export const getServerSideProps: GetServerSideProps = async (context) => {
    const params = context.params as { id?: string };

    if (!params.id) {
        return {
            notFound: true,
        };
    }

    try {
        const { data } = await todosRequests.getTodoById(Number(params.id));

        console.log(data, 'fetchedTodo');
        if (!data) {
            return {
                notFound: true,
            };
        }

        return {
            props: {
                todo: data,
            },
        };
    } catch {
        return {
            redirect: {
                destination: '/',
                permanent: false,
            },
        };
    }
};

export default TodoPage;
